export const formatResponseTillage = response => {
	let tempArray = [];
	response.forEach(res => {
		tempArray.push({
			id: res.id,
			type: res.typeTillage,
			subarea: res.subArea || 'Sub-√°rea 9 ‚ÄúAntiga matinha‚Äù - 60 ha',
			sackQuantity: res.sackQuantity,
			weightPerSack: res.weightPerSack,
		});
	});

	return tempArray;
};
